<template>
  <div class="DashboardOption">
    <div class="title">
      <div>Ответ {{ index + 1 }}
        <el-button
          type="warning"
          title="Удалить"
          plain
          size="mini"
          icon="el-icon-delete"
          class="delete-option"
          @click="$emit('delete', option)" />
      </div>

      <div class="user-input">
        <el-form-item
          size="small"
          label="Пользовательский ввод"
          label-width="178px">
          <el-switch
            v-model="option.input_field" />
        </el-form-item>

        <template v-if="option.input_field">
          <el-form-item
            size="small"
            label="Тип"
            label-width="40px">
            <el-select
              v-model="option.input_field_type">
              <el-option
                label="Текст"
                value="string" />
              <el-option
                label="Число"
                value="number" />
              <el-option
                label="Дата"
                value="date" />
            </el-select>
          </el-form-item>
          <el-form-item
            v-if="option.input_field_type !== 'date' "
            size="small"
            label="Макс."
            label-width="50px">
            <el-input-number
              :min="100"
              v-model="option.input_field_size" />
          </el-form-item>
        </template>
      </div>
    </div>
    <div class="base">
      <el-form-item
        size="mini"
        label="Значение"
        label-width="80px">
        <el-input v-model="option.description" />
      </el-form-item>
      <el-form-item
        size="mini">
        <el-input
          v-model="option.hint"
          placeholder="Подсказка" />
      </el-form-item>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    index: {
      type: Number,
      default: 0,
    },
    option: {
      type: Object,
      default: null,
    },
  },

  methods: {
    async deleteOption() {
      await this.$http.delete(`options/${this.option.id}`);
      this.$message.success('Ответ успешно удален');
      this.$emit('deleted', this.option);
    },
  },
};
</script>

