<template>
  <transition
    name="highlight"
    appear>
    <div
      v-if="question"
      :key="id"
      class="DashboardQuestion">
      <div class="title">
        <div>Вопрос {{ index + 1 }}
          <span class="small">(QID {{ id }})</span>
        </div>
        <el-button
          type="warning"
          plain
          size="small"
          class="delete-question"
          @click="deleteQuestion">Удалить вопрос</el-button>
      </div>
      <el-form
        :model="question"
        label-position="left"
        class="question-form">
        <el-form-item>
          <el-input
            v-model="question.description"
            placeholder="Текст вопроса" />
        </el-form-item>
        <div class="multiple">
          <el-form-item label="Можно выбрать несколько ответов">
            <el-switch v-model="question.multiple" />
          </el-form-item>
          <template v-if="question.multiple">
            <el-form-item label="Минимум">
              <el-input-number
                v-model="question.min"
                :min="1" />
            </el-form-item>
            <el-form-item label="Максимум">
              <el-input-number
                v-model="question.max"
                :min="1" />
            </el-form-item>
          </template>
        </div>

        <div class="options">
          <transition-group
            name="highlight"
            tag="div"
            appear>
            <dashboard-option
              v-for="(option, index) in question.options"
              :key="option.id"
              :option="option"
              :index="index"
              @delete="deleteOption(option)" />
          </transition-group>
          <el-button
            type="text"
            class="add-option"
            @click="addOption">Добавить ответ</el-button>
          <div class="divider" />
        </div>

        <div class="bottom">
          <el-button
            type="primary"
            @click="updateQuestion">Сохранить</el-button>
          <div class="hint">Сохраните вопрос после внесения изменений</div>
        </div>
      </el-form>
    </div>
  </transition>
</template>

<script>
import DashboardOption from '@/components/dashboard/DashboardOption.vue';

export default {
  components: {
    DashboardOption,
  },

  props: {
    index: {
      type: Number,
      default: 0,
    },
    id: {
      type: [Number, String],
      default: null,
    },
  },

  data() {
    return {
      question: null,
    };
  },

  mounted() {
    this.getQuestion();
  },

  methods: {
    async getQuestion() {
      const response = await this.$http.get(`questions/${this.id}`);
      this.question = response.data;
    },

    async updateQuestion() {
      // Currently not working by API
      // this.$emit('update-survey');
      // ###
      this.question.options.forEach((option) => {
        if (option.id) {
          this.$http.patch(`options/${option.id}`, { option });
        } else {
          this.$http.post('options', { option });
        }
      });
      await this.$http.patch(`questions/${this.id}`, { question: this.question });
      this.$message.success('Вопрос успешно обновлен');
      this.getQuestion();
    },

    async deleteQuestion() {
      await this.$http.delete(`questions/${this.id}`);
      this.$message.success('Вопрос был удален');
      this.$emit('updated');
    },

    async addOption() {
      const response = await this.$http.post('options', { option: { question_id: this.id, description: '' } });
      this.question.options.push(response.data);
    },

    async deleteOption(option) {
      await this.$http.delete(`options/${option.id}`);
      this.question.options = this.question.options.filter(e => e !== option);
      this.$message.success('Ответ успешно удален');
    },
  },
};
</script>

