<template>
  <div
    v-if="survey"
    class="DashboardSurveysShow"
  >
    <!-- ### START TOOLBAR ### -->
    <div class="toolbar">
      <div class="group">
        <div class="title">
          <router-link :to="{name: 'DashboardSurveys'}">Опросы</router-link>
          <div class="slash"><i class="el-icon-arrow-right" /></div>
          <div class="name truncate">{{ survey.name }}</div>
        </div>
        <survey-status :survey="survey" />
        <template v-if="!survey.archived_at">
          <el-button
            v-if="!survey.published"
            type="text"
            @click="publishSurvey"
          >
            Опубликовать
          </el-button>
          <el-button
            v-if="!survey.published"
            type="text"
            @click="archiveSurvey"
          >
            Архивировать
          </el-button>
          <el-button
            v-if="survey.published"
            type="text"
            @click="unpublishSurvey"
          >
            Снять с публикации
          </el-button>
        </template>
      </div>
      <div class="group">
        <router-link :to="{ name: 'DashboardSurveysStatistics' }">
          <el-button
            type="success"
            size="small"
            class="statistics"
          >
            Результаты
            <i class="fa fa-fw fa-chart-pie" />
          </el-button>
        </router-link>
      </div>
    </div>
    <!-- ### END TOOLBAR ### -->

    <!-- ### START CONTAINER ### -->
    <div class="container">
      <el-form
        ref="surveyForm"
        :model="survey"
        class="survey-form"
      >
        <div class="body">
          <h3 class="title">
            Общая информация
            <span class="small">(SID {{ id }})</span>
          </h3>
          <el-form-item
            v-loading="isSendingAttachment"
            :required="true"
          >
            <label class="file">
              <input
                type="file"
                hidden
                @change="getAttachment"
              >

              <div v-if="cover">
                <img
                  :src="cover"
                  alt="Обложка"
                  class="cover"
                >

                <div class="hint">Нажмите для изменения обложки</div>
              </div>
              <div
                v-else
                class="not-cover"
              >
                Загрузить обложку
              </div>
            </label>
          </el-form-item>
          <el-form-item
            :required="true"
          >
            <el-input
              v-model="survey.name"
              placeholder="Название"
            />
          </el-form-item>
          <el-form-item>
            <el-input
              v-model="survey.description"
              placeholder="Описание"
            />
          </el-form-item>
          <el-form-item>
            <el-input
              v-model="survey.start_message"
              placeholder="Приветственное сообщение"
            />
          </el-form-item>
          <el-form-item>
            <el-input
              v-model="survey.finish_message"
              placeholder="Сообщение по окончании"
            />
          </el-form-item>
          <!--el-form-item>
            <el-input
              v-model="survey.hint"
              placeholder="Базовая подсказка"
            />
          </el-form-item-->
          <div class="inline">
            <el-form-item label="Цвет опроса">
              <el-color-picker
                v-model="survey.color"
              />
            </el-form-item>
            <el-form-item label="Тематика">
              <el-select
                v-model="survey.type"
                placeholder="Выберите тематику"
              >
                <el-option
                  v-for="option in surveyTypeOptions"
                  :key="option.key"
                  :label="option.value"
                  :value="option.key"
                />
              </el-select>
            </el-form-item>
            <el-form-item label="Баллов за прохождение">
              <el-input-number
                :min="0"
                v-model="survey.price"
              />
            </el-form-item>
          </div>
          <el-form-item label="Анонимный опрос">
            <el-switch v-model="survey.anonymous" />
          </el-form-item>
          <el-form-item label="Доступ только по ссылке">
            <el-switch v-model="survey.link_only" />
          </el-form-item>
          <el-form-item label="Публичная статистика">
            <el-switch v-model="survey.public_stats" />
          </el-form-item>
        </div>
        <el-form-item class="bottom">
          <el-button
            :disabled="isSendingAttachment"
            type="primary"
            @click="updateSurvey"
          >
            Сохранить
          </el-button>
        </el-form-item>
      </el-form>

      <div class="questions">
        <dashboard-question
          v-for="(question, index) in survey.questions"
          :key="question.id"
          :id="question.id"
          :index="index"
          @updated="getSurvey"
        />

        <div class="new-question">
          <h3 class="title">
            Добавить вопрос
          </h3>
          <el-input
            v-model="newQuestion.description"
            placeholder="Текст вопроса"
          />
          <el-button
            type="primary"
            @click="createQuestion"
          >
            Создать вопрос
          </el-button>
        </div>

      </div>
    </div>
    <!-- ### END CONTAINER ### -->
  </div>
</template>

<script>
import DashboardQuestion from '@/components/dashboard/DashboardQuestion.vue';
import SurveyStatus from '@/components/SurveyStatus.vue';

export default {
  name: 'DashboardSurveysShow',

  components: {
    DashboardQuestion,
    SurveyStatus,
  },

  props: {
    id: {
      type: [Number, String],
      default: 0,
    },
  },

  data() {
    return {
      survey: null,
      newQuestion: {
        description: '',
        survey_id: this.id,
      },
      cover: undefined,
      attachment: undefined,
      isSendingAttachment: false,
    };
  },

  computed: {
    surveyTypeOptions() {
      return this.$store.state.surveys.types;
    },
  },

  async mounted() {
    await this.getSurvey();
    this.setCover();
  },

  methods: {
    async getSurvey() {
      const response = await this.$http.get(`surveys/${this.id}`);
      this.survey = response.data;
    },

    setCover() {
      const uri = this.survey.cover_url
        ? `${process.env.VUE_APP_API}${this.survey.cover_url}`
        : '';

      this.cover = uri;
    },

    // На случай, если FileReader не справится: https://github.com/transloadit/uppy
    getAttachment({ target: { files: [file] } }) {
      // Для обложки.
      const readerCover = new FileReader();
      readerCover.readAsDataURL(file);

      readerCover.onload = () => {
        this.cover = readerCover.result;
      };

      readerCover.onerror = (err) => {
        console.error('Get Attachment Cover Error:', err);
      };

      // Отправка на сервер.
      const reader = new FileReader();
      reader.readAsArrayBuffer(file);

      reader.onload = async () => {
        this.attachment = reader.result;
        await this.sendAttachment();
      };

      reader.onerror = (err) => {
        console.error('Get Attachment Error:', err);
      };
    },

    async sendAttachment() {
      this.isSendingAttachment = true;

      const data = new FormData();
      data.append('file', new Blob([this.attachment]));

      try {
        await this.$http({
          method: 'post',
          url: `/surveys/${this.id}/cover`,
          config: { headers: { 'Content-Type': 'multipart/form-data' } },
          data,
        });

        this.$message.success('Обложка успешно загружена!');
      } catch (err) {
        this.$message.error('Обложка не загружена!');
        console.dir(err);
      } finally {
        this.isSendingAttachment = false;
      }
    },

    async archiveSurvey() {
      try {
        await this.$confirm('Опрос будет заархивирован. Вы уверены?', 'Архивация опроса', {
          confirmButtonText: 'Архивировать',
          cancelButtonText: 'Отменить',
          type: 'warning',
        });
        await this.$http.patch(`surveys/${this.id}`, { survey: { archive: true } });
        this.$message.success('Вы заархивировали опрос');
        this.$router.push({ name: 'DashboardSurveys' });
      } catch (e) {
        this.$message.warning('Опрос не был заархивирован.');
      }
    },

    async updateSurvey() {
      this.$http.patch(`/surveys/${this.survey.id}`, { survey: this.survey })
        .then(() => {
          this.$message.success('Опрос обновлен');
          this.getSurvey();
        });
    },

    async publishSurvey() {
      try {
        await this.$confirm(
          'После публикации, опрос будет открыт для народного голосования. Убедитесь, что опрос составлен корректно и без ошибок - на время публикации редактирование будет недоступно.',
          'Публикация опроса',
          {
            confirmButtonText: 'Опубликовать',
            cancelButtonText: 'Отменить',
            type: 'success',
          },
        );
        this.survey.published = true;
        await this.$http.patch(`/surveys/${this.survey.id}`, { survey: this.survey });
        this.$message.success('Опрос был успешно опубликован');
        this.getSurvey();
      } catch (e) {
        this.$message.warning('Опрос не был опубликован.');
      }
    },

    async unpublishSurvey() {
      try {
        await this.$confirm(
          'Снимая с публикации, вы закрываете публичный доступ к опросу.',
          'Снять с публикации',
          {
            confirmButtonText: 'Снять с публикации',
            cancelButtonText: 'Отменить',
            type: 'warning',
          },
        );
        this.survey.published = false;
        await this.$http.patch(`/surveys/${this.survey.id}`, { survey: this.survey });
        this.$message.success('Опрос был снят с  публикации');
        this.getSurvey();
      } catch (e) {
        this.$message.warning('Опрос не был снят с публикации');
      }
    },

    async createQuestion() {
      await this.$http.post('questions', { question: this.newQuestion });
      this.$message.success('Вопрос был успешно добавлен');
      this.getSurvey();
      this.newQuestion.description = '';
    },
  },
};
</script>

