<template>
  <div class="SurveyStatus">
    <el-tag
      :type="statusType"
      disable-transitions>
      {{ statusLabel }}
    </el-tag>
  </div>
</template>

<script>
// Статус опроса формируется динамически на основе полей published:Boolean && archived_at
// Если есть атрибут archived_at - то опрос архивный и восстановлению/изменению не подлежит
// Иначе: published ? опубликован : черновик - можно менять неограниченное количество раз
export default {
  props: {
    survey: {
      type: Object,
      default: () => ({}),
    },
  },
  computed: {
    statusLabel() {
      if (this.survey.archived_at) return 'Архив';
      if (this.survey.published) return 'Опубликован';
      return 'Черновик';
    },

    statusType() {
      if (this.survey.archived_at) return 'info';
      if (this.survey.published) return 'success';
      return 'warning';
    },
  },
};
</script>

